// 
.container {
  @include make-container();
  @if $enable-responsive {
    max-width: 100%;
    @include make-container-max-widths();
  } @else {
    width: 100%;
  }
}

// 
//  100%  pc 
@if $enable-responsive {
  .container-fluid {
    width: 100%;
    @include make-container();
  }
}

//  ie6+
.container-equal-height {
  display: block;
  overflow: hidden;

  >div {
    padding-bottom: 30000px;
    margin-bottom: -30000px;
  }
  .container-equal-height-nav {
    display: block;
    float: left;
  }
  .container-equal-height-main {
    display: block;
    float: left;
    width: 100%;
  }
}

// float 
.container-fluid-float {
  @include clearfix();
  .container-fluid-float-cell {
    float: left;
  }
  .container-fluid-float-full {
    overflow: hidden;
  }
}

// 
.container-square {
  position: relative;
  display: block;
  width: 100%;
  height: 0;
  padding-top: 100%;
  .container-square-box {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: block;
  }
}
