// 
// https://material.io/guidelines/motion/duration-easing.html#duration-easing-common-durations
$animation-ease-out: cubic-bezier(0, 0, .2, 1) !default; //  deceleration
$animation-ease-in-out: cubic-bezier(.4, 0, .2, 1) !default; //  standard
$animation-ease-in: cubic-bezier(.4, 0, 1, 1) !default; //  acceleration
$animation-sharp: cubic-bezier(.4, 0, .6, 1) !default; //  sharp
