// sass-lint:disable no-duplicate-properties
// reset
//  normalize.css 

//  html
//  1.  border-box padding border
//   padding border 
//   border-box 
//   border-box 
// https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
//  2. https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/7165383/
//  ie11  edge 
// 
// 3.  sans-serif
// 4.  IOS 
//  html 
//  rem  html 

html {
  //box-sizing: border-box; // 1
  // rem  html 
  -ms-overflow-style: scrollbar; // 2
  font-family: sans-serif; // 3
  font-weight: $font-weight-base;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); // 4
}

//  border-box
/**,
*::before,
*::after {
  box-sizing: inherit;
}*/

//  ie  device-width
//  IE 10+  <meta name="viewport">
@at-root {
  @-ms-viewport {
    width: device-width;
  }
}

// 1. 
body {
  font-family: $font-family;
  // rem  body 
  font-size: $font-size-base;
  font-weight: $font-weight-base;
  line-height: $line-height-base;
  color: $body-color;
  background-color: $body-bg; // 1
}

// normalize.scss  IE 9- 
dialog,
figcaption,
figure,
hgroup,
main {
  display: block;
}

// Credit: https://github.com/suitcss/base
//  tabindex  -1 
[tabindex="-1"]:focus {
  outline: none !important;
}

//
// 
//

//  margin-top margin-bottom
h1, h2, h3, h4, h5, h6 {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

//  p 
//  rem  margin-top margin-bottom
p {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

// Abbreviations
//
// 1. Remove the bottom border in Chrome 57- and Firefox 39-.
// 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
// 3. Add explicit cursor to indicate changed behavior.
// 4. Duplicate behavior to the data-* attribute for our tooltip plugin

abbr[title],
abbr[data-original-title] { // 4
  border-bottom: 0; // 1
  text-decoration: underline; // 2
  text-decoration: underline dotted; // 2
  cursor: help; // 3
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 1rem;
  margin-left: 1.25rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: $dt-font-weight;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0; // Undo browser default
}

blockquote {
  margin: 0 0 1rem;
}

//
// Links
//

a {
  color: $link-color;
  text-decoration: $link-decoration;

  @include hover {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
  }
}

//  href  tabindex  a 
a:not([href]):not([tabindex]) {
  text-decoration: none;

  @include hover-focus {
    color: inherit;
    text-decoration: none;
  }

  &:focus {
    outline: 0;
  }
}

//
// Code
//

pre {
  // Remove browser default top margin
  margin-top: 0;
  // Reset browser default of `1em` to use `rem`s
  margin-bottom: 1rem;
  // Don't allow content to break outside
  overflow: auto;
}

//
// Figures
//

figure {
  // Apply a consistent margin strategy (matches our type styles).
  margin: 0 0 1rem;
}

//
// 
//

img {
  // 
  vertical-align: middle;
}

/**
*  300ms 
*  touch-action: manipulation 
* 
* https://developer.mozilla.org/zh-CN/docs/Web/CSS/touch-action
* http://caniuse.com/#feat=css-touch-action
* http://patrickhlauke.github.io/touch/tests/results/#suppressing-300ms-delay
**/
a,
area,
button,
[role="button"],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

//
// Table 
//

table {
  border-collapse: collapse; // Prevent double borders
}

caption {
  padding-top: $table-cell-padding;
  padding-bottom: $table-cell-padding;
  color: $text-muted;
  text-align: left;
  caption-side: bottom;
}

th {
  // Matches default `<td>` alignment
  text-align: left;
}

//
//  Forms 
//

label {
  display: inline-block;
}

input,
button,
select,
optgroup,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  // Remove the default appearance of temporal inputs to avoid a Mobile Safari
  // bug where setting a custom line-height prevents text from being vertically
  // centered within the input.
  // See https://bugs.webkit.org/show_bug.cgi?id=139848
  -webkit-appearance: listbox;
}

textarea {
  // Textareas should really only resize vertically so they don't break their (horizontal) containers.
  resize: vertical;
}

fieldset {
  // Browsers set a default `min-width: min-content;` on fieldsets,
  // unlike e.g. `<div>`s, which have `min-width: 0;` by default.
  // So we reset that to ensure fieldsets behave more like a standard block element.
  // and https://html.spec.whatwg.org/multipage/#the-fieldset-and-legend-elements
  min-width: 0;
  // Reset the default outline behavior of fieldsets so they don't affect page layout.
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
}

[type="search"] {
  // Correct the odd appearance in Chrome and Safari.
  -webkit-appearance: none;
}

//
// Correct element displays
//

output {
  display: inline-block;
}
