// ripple style
@import "../../animation/function";
@import "../../theme/mixin";
@import "mixin";

.ripple {
  position: relative;
  overflow: hidden;
  outline:  none;
  @include ripple-base;
  //  ripple  ::before ::after 
  @include ripple-bg((pseudo: "::before"));
  @include ripple-bg((pseudo: "::after"));
}

//  css  .radio.ripple-bg(--ripple-bg-color: rgba(0, 0, 0, .12));
// --ripple-bg-color: rgba(0, 0, 0, .06);
.ripple-bg::before,
.ripple-bg::after {
  // FIXME  sass-lint  css variables #{} 
  // https://github.com/sasstools/sass-lint/issues/877
  //  sass-lint  #{}

  // 
  background-color: var(--ripple-bg-color, rgba(0, 0, 0, .08));
}

//  JavaScript
.ripple-impact {
  @include ripple-impact();
  // 
  @include ripple-bg-impact((pseudo: "::before"));
}

// 
@include ripple-unbounded();

// 
@each $style in ("primary", "accent") {
  .ripple-#{$style} {
    &::before,
    &::after {
      // 
      @include theme-prop(background-color, #{$style});
      // ripple 
      @include ripple-theme((theme-style: #{$style}, opacity: .16));
    }
  }
}

//  ripple
@each $theme, $color in $theme-assist-colors {
  .ripple-#{$theme} {
    &::before,
    &::after {
      @include theme-assist(background-color, $theme, $color, .16);
    }
  }
}



