// 
//  ul ol div 

// 
.list {
  display: block;
  padding-left: 0;
  margin: .75rem 0 0;

  //  ul ol list-style-type  none
  list-style-type: none;
}

//  a  button   .list-item  .list-item   active 
.list-item-action {
  width: 100%; //  button  100%
  color: $list-action-color;
  text-align: inherit; //  button
  cursor: pointer;

  // Hover state
  @include hover-focus {
    color: $list-action-hover-color;
    background-color: $list-hover-bg;
    text-decoration: none;
  }

  &:active {
    color: $list-action-active-color;
    background-color: $list-action-active-bg;
  }
}

//  button
button.list-item-action {
  border: 0;

  &:focus {
    outline: 0;
  }
}

// list 
.list-item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: $list-item-padding-y $list-item-padding-x;
  background-color: $list-bg;

  //  a 
  @include hover-focus {
    text-decoration: none;
  }

  // 
  &.active {
    color: $list-active-color;
    background-color: $list-active-bg;
  }

  // 
  &.disabled,
  &:disabled {
    color: $list-disabled-color;
    background-color: $list-disabled-bg;
  }
}

.list-dense {
  font-size: .75rem;

  > .list-item {
    padding: $list-dense-item-padding-y $list-item-padding-x;
  }
}

//  list

// 
.list-border {
  .list-item {
    // 
    margin-bottom: -$list-border-width;
    border: $list-border-width solid $list-border-color;
  }
}

// 
.list-border-inner {
  .list-item {
    border-top: $list-border-width solid $list-border-color;
    &:first-child {
      border-top: 0;
    }
  }
}

// 
.list-spacing {
  .list-item {
    margin-bottom: $list-border-spacing;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

//  24px 16px + 8px = 1.5rem
// 
.list-item-start-detail,
.list-item-end-detail {
  width: $list-item-detail-size;
  height: $list-item-detail-size;
  margin-right: $list-item-detail-margin;
}

.list-item-start-detail {
  margin-left: 0;
}

.list-item-end-detail {
  margin-left: auto;
}

// 
.list-dense {
  .list-item-start-detail,
  .list-item-end-detail {
    width: $list-item-detail-size * .8333;
    height: $list-item-detail-size * .8333;
    margin-right: $list-item-detail-margin;
  }
}

//  avatar
.list-item-avatar {
  width: $list-item-avatar-size;
  height: $list-item-avatar-size;
  margin-left: 0;
  margin-right: $list-item-avatar-margin;
  border-radius: 50%;
}

// 
.list-item-text-center {
  width: 100%;
  text-align: center;

  + .list-item-end-detail {
    position: absolute;
    right: $list-item-padding-x;
  }
}
// 
.list-item-text-right {
  margin-left: auto;
  margin-right: $list-item-text-right-margin-right;
  font-size: $list-item-text-right-font-size;

  + .list-item-end-detail {
    margin-left: inherit;
  }
}

// 
.list-item-text {
  display: inline-flex;
  flex-direction: column;
}

.list-item-text-secondary {
  font-size: $list-item-text-secondary-font-size;
  font-weight: $list-item-text-secondary-font-weight;
  line-height: $list-item-text-line-height;
  color: $light-black;
}

.list-dense {
  .list-item-text-secondary {
    font-size: $list-dense-font-size;
  }
}

// 
.list-divider {
  width: 100%;
  height: 0;
  margin: 0;
  border: 0;
  border-bottom: 1px solid $faint-black;
}

.list-divider-inset {
  width: calc(100% - #{$list-divider-inset-left});
  margin-left: $list-divider-inset-left;
  margin-right: 0;
}

//  list-item-start-detail 
.list-divider-inset-detail {
  width: calc(100% - #{$list-divider-inset-detail-left});
  margin-left: $list-divider-inset-detail-left;
  margin-right: 0;
}

//  list-item-avatar 
.list-divider-inset-avatar {
  width: calc(100% - #{$list-divider-inset-avatar-left});
  margin-left: $list-divider-inset-avatar-left;
  margin-right: 0;
}

// 

.list-group-header {
  margin: $list-group-header-padding-y $list-group-header-padding-x;
  font-size: $list-group-header-font-size;
  font-weight: $list-group-header-font-weight;
  color: $dark-black;
}
