//  css 

@import "./mixin";

//  css 
:root {
  @each $style in map-keys($theme-property-values) {
    // FIXME  sass-lint  css variables #{} 
    // https://github.com/sasstools/sass-lint/issues/877
    #{--theme-#{$style}}: map-get($theme-property-values, $style);
  }

  // 
  @each $theme, $color in $theme-assist-colors {
    // FIXME  sass-lint  css variables #{} 
    // https://github.com/sasstools/sass-lint/issues/877

    #{--theme-#{$theme}}: $color;
  }

  // 
  @each $theme, $color in $theme-assist-colors-light {
    // FIXME  sass-lint  css variables #{} 
    // https://github.com/sasstools/sass-lint/issues/877

    #{--theme-#{$theme}-light}: $color;
  }
}

// 
// .theme-background {background-color: #fff}
.theme-background {
  @include theme-prop(background-color, background);
}

//  primary  accent 
@each $style in ("primary", "accent") {
  .theme-#{$style}-bg {
    @include theme-prop(background-color, $style, true);
  }
}

// 
// .theme-text-primary-on-primary {color: #ffffff !important;}
@each $style in map-keys($theme-property-values) {
  @if $style != "background" {
    .theme-#{$style} {
      @include theme-prop(color, $style, true);
    }
  }
}

