/**
* 
{
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
}
 */

/**
* 
* $name 
* $breakpoint 
* $breakpoint-names 
*  breakpoint-next(sm)   md
* breakpoint-next(sm, (xs: 0, sm: 576px, md: 768px))  md
* breakpoint-next(sm, $breakpoint-names: (xs sm md))  md
* breakpoint-next(md, $breakpoint-names: (xs sm md))  null
 */
@function breakpoint-next($name, $breakpoints: $responsive-breakpoints, $breakpoint-names: map-keys($breakpoints)) {
  $n: index($breakpoint-names, $name);
  @return if($n < length($breakpoint-names), nth($breakpoint-names, $n + 1), null);
}

/**
*  0 null
* breakpoint-min(sm, (xs: 0, sm: 576px, md: 768px))  576px
* breakpoint-min(xs, (xs: 0, sm: 576px, md: 768px))  null
 */
@function breakpoint-min($name, $breakpoints: $responsive-breakpoints) {
  $min: map-get($breakpoints, $name);
  @return if($min != 0, $min, null);
}

/**
*  - 1px  null
* breakpoint-max(sm, (xs: 0, sm: 576px, md: 768px))  767px
* breakpoint-max(md, (xs: 0, sm: 576px, md: 768px))  null
 */
@function breakpoint-max($name, $breakpoints: $responsive-breakpoints) {
  $next: breakpoint-next($name, $breakpoints);
  @return if($next, breakpoint-min($next, $breakpoints) - 1px, null);
}

/**
*  $name 
* breakpoint-infix(xs, (xs: 0, sm: 576px, md: 768px))  ""
* breakpoint-infix(md, (xs: 0, sm: 576px, md: 768px))  "-md"
 */
@function breakpoint-infix($name, $breakpoints: $responsive-breakpoints) {
  @return if(breakpoint-min($name, $breakpoints) == null, "", "-#{$name}");
}

/**
*  $name  xs 
* 
.test {
  @include media-breakpoint-up(sm) {
    color: red;
  }
}

@media (min-width: 576px) {
  .test {
    color: red;
  }
}

.test {
  @include media-breakpoint-up(sm) {
    color: red;
  }
}

.test {
  color: red;
}
 */
@mixin media-breakpoint-up($name, $breakpoints: $responsive-breakpoints) {
  $min: breakpoint-min($name, $breakpoints);
  @if $min {
    @media (min-width: $min) {
      @content;
    }
  } @else {
    @content;
  }
}

/**
*  media-breakpoint-up   $name  xl 
* 
.test {
  @include media-breakpoint-down(sm) {
    color: red;
  }
}

@media (max-width: 767px) {
  .test {
    color: red;
  }
}

.test {
  @include media-breakpoint-down(xl) {
    color: red;
  }
}

.test {
  color: red;
}
 */
@mixin media-breakpoint-down($name, $breakpoints: $responsive-breakpoints) {
  $max: breakpoint-max($name, $breakpoints);
  @if $max {
    @media (max-width: $max) {
      @content;
    }
  } @else {
    @content;
  }
}

/**
* 
.test {
  @include media-breakpoint-between(sm, lg) {
    color: red;
  }
}

@media (min-width: 576px) and (max-width: 1199px) {
  .test {
    color: red;
  }
}
 */
@mixin media-breakpoint-between($lower, $upper, $breakpoints: $responsive-breakpoints) {
  @include media-breakpoint-up($lower, $breakpoints) {
    @include media-breakpoint-down($upper, $breakpoints) {
      @content;
    }
  }
}

// Media between the breakpoint's minimum and maximum widths.
// No minimum for the smallest breakpoint, and no maximum for the largest one.
// Makes the @content apply only to the given breakpoint, not viewports any wider or narrower.

/**
*  sm  576px  767px
.test {
  @include media-breakpoint-only(sm) {
    color: red;
  }
}

@media (min-width: 576px) and (max-width: 767px) {
  .test {
    color: red;
  }
}
 */
@mixin media-breakpoint-only($name, $breakpoints: $responsive-breakpoints) {
  @include media-breakpoint-between($name, $name, $breakpoints) {
    @content;
  }
}

/*  $enable-breakpoint  false */
@mixin no-media-breakpoint() {
  @content;
}

